import React from 'react';
import _ from 'lodash';
import {graphql} from 'gatsby';

import components, {Layout} from '../components/index';
import Header from '../components/Header';
import Footer from '../components/Footer';

export default class Home extends React.Component {
    render() {
        return (
            <Layout {...this.props}>
            <div id="wrapper">
                <Header {...this.props} />
                <div id="main">
                    {_.map(_.get(this.props, 'data.page.frontmatter.sections'), (section, section_idx) => {
                        let GetSectionComponent = components[_.get(section, 'component')];
                        return (
                            <GetSectionComponent key={section_idx} {...this.props} section={section} />
                        )
                    })}
                </div>
                <Footer {...this.props} />
            </div>
            </Layout>
        );
    }
}


export const query = graphql`
    query($url: String!) {
        page: markdownRemark(fields: { url: { eq: $url } }) {
            frontmatter {
                sections {
                    component
                    element_id
                    title
                    image_url
                    image_alt
                    content
                }
                header {
                    title
                    subtitle
                }
            }
        }
        site {
            siteMetadata {
                title
            }
        }
    }
`;
