import React from 'react';
import _ from 'lodash';
import {withPrefix} from 'gatsby';

import markdownify from '../utils/markdownify';

export default class SectionSimple extends React.Component {
    render() {
        return (
            <article id={_.get(this.props, 'section.element_id')}>
                <h2 className="major">{_.get(this.props, 'section.title')}</h2>
                {_.get(this.props, 'section.image_url') && 
                    <span className="image main"><img src={withPrefix(_.get(this.props, 'section.image_url'))} alt={_.get(this.props, 'section.image_alt')} /></span>
                }
                {markdownify(_.get(this.props, 'section.content'))}
            </article>
        );
    }
}
