import React from 'react';
import _ from 'lodash';

import markdownify from '../utils/markdownify';

export default class Header extends React.Component {
    render() {
        return (
            <header id="header">
                <div className="logo">
                    <span className="icon fa-diamond" />
                </div>
                <div className="content">
                    <div className="inner">
                        <h1>{_.get(this.props, 'data.page.frontmatter.header.title')}</h1>
                        {markdownify(_.get(this.props, 'data.page.frontmatter.header.subtitle'))}
                    </div>
                </div>
                <nav>
                    <ul>
                        {_.map(_.get(this.props, 'data.page.frontmatter.sections'), (section, section_idx) => (
                            <li key={section_idx}><a href={'#' + _.get(section, 'element_id')}>{_.get(section, 'title')}</a></li>
                        ))}
                    </ul>
                </nav>
            </header>
        );
    }
}
